.container {
  display: flex;
  justify-content: center;

  &__main {
    display: flex;
    flex-wrap: wrap;
    width: $container-width;
    min-width: $container-width;
    padding: 50px 0;
  }
  &__card {
    margin-bottom: 46px;

    &:not(:nth-child(4n)) {
      margin-right: $margin-right;
    }
  }
}
